@import url('https://fonts.googleapis.com/css2?family=Teko:wght@300&display=swap');



.parent {
  position:fixed;
  display: flex;
  top: 200px;
  left: 215px;
  z-index: 999;
  width: 100%;
  height: 23px;
}

.close {
  position:fixed;
  display: flex;
  top: 200px;
  right: 215px;
  z-index: 999;
}

.cart {
  justify-content: center;
  width: 1000px;
  height: 500px;
  text-align: center;
  background-color: var(--light);
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  border-radius: .5rem;
}

.cart h2 {
  font-family:'Teko', sans-serif;
  font-size: 2.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: .5rem;
  margin: 1rem 0;
}



.cart h3 {
  font-family:'Teko', sans-serif;
  font-size:10vw;
}

.cart .close {
  position: sticky;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  color: red;
}

.cart .close:hover {
  text-decoration: underline;
}

.cart-closed {
  position: fixed;
  top: .5%;
  right: .5%;
  font-size: 2rem;
  cursor: pointer;
  border-radius: 50%;
  padding: .25rem;
  width: 50px;
  height: 50px;
}

.cart-closed:hover {
  transform: rotate(8deg);
}


.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}